import { generatePath } from 'react-router-dom';
import { createAsyncThunk } from '@reduxjs/toolkit';
import { ApiRoute } from '../../const';
import { adaptTournamentsToClient, adaptTournamentToClient } from '../../adapters/tournament-adapter';
import { adaptPaginationToClient } from '../../adapters/pagination-adapter';
export const fetchTournaments = createAsyncThunk('tournaments/fetchTournaments', async ({ sort, order, count, page, keyword, onSuccess }, { extra: api }) => {
    const { data } = await api.get(`${ApiRoute.TOURNAMENTS}?sort=${sort}&order=${order}&count=${count}&page=${page}&keyword=${keyword ?? ''}`);
    onSuccess({
        tournaments: adaptTournamentsToClient(data.data),
        pagination: adaptPaginationToClient(data),
    });
});
export const fetchPreviousTournaments = createAsyncThunk('tournaments/fetchPreviousTournaments', async ({ sort, order, count, page, onSuccess }, { extra: api }) => {
    const { data } = await api.get(`${ApiRoute.TOURNAMENTS_PREVIOUS}?sort=${sort}&order=${order}&count=${count}&page=${page}`);
    onSuccess({
        tournaments: adaptTournamentsToClient(data.data),
        pagination: adaptPaginationToClient(data),
    });
});
export const fetchUpcomingTournaments = createAsyncThunk('tournaments/fetchUpcomingTournaments', async ({ sort, order }, { extra: api }) => {
    const { data } = await api.get(`${ApiRoute.TOURNAMENTS_UPCOMING}?sort=${sort}&order=${order}`);
    const tournaments = adaptTournamentsToClient(data);
    return tournaments;
});
export const fetchTournamentBySlug = createAsyncThunk('tournaments/fetchTournamentBySlug', async ({ slug, onSuccess }, { extra: api }) => {
    const { data } = await api.get(generatePath(ApiRoute.TOURNAMENTS_SELECTED, { slug }));
    const tournament = adaptTournamentToClient(data);
    onSuccess(tournament);
});
export const storeTournament = createAsyncThunk('tournaments/storeTournament', async ({ form, onSuccess }, { extra: api }) => {
    const { data } = await api.post(ApiRoute.TOURNAMENTS, form);
    onSuccess(adaptTournamentToClient(data.tournament));
});
export const updateTournament = createAsyncThunk('tournaments/updateTournament', async ({ form, onSuccess }, { extra: api }) => {
    const { data } = await api.post('/api/tournaments/update', form);
    onSuccess(adaptTournamentToClient(data.tournament));
});
export const deleteTournaments = createAsyncThunk('tournaments/deleteTournaments', async ({ ids, onSuccess }, { extra: api }) => {
    await api.post('/api/tournaments/delete', { ids });
    onSuccess();
});
