import { adaptNewsArrayToClient, adaptNewsToClient } from './../../adapters/news-adapter';
import { generatePath } from 'react-router-dom';
import { ApiRoute } from '../../const';
import { createAsyncThunk } from '@reduxjs/toolkit';
import { adaptPaginationToClient } from '../../adapters/pagination-adapter';
export const fetchNews = createAsyncThunk('news/fetchNews', async ({ sort, order, count, page, keyword, onSuccess }, { extra: api }) => {
    const { data } = await api.get(`${ApiRoute.NEWS}?sort=${sort}&order=${order}&count=${count}&page=${page}&keyword=${keyword ?? ''}`);
    onSuccess({
        news: adaptNewsArrayToClient(data.data),
        pagination: adaptPaginationToClient(data),
    });
});
export const fetchNewsBySlug = createAsyncThunk('news/fetchNewsBySlug', async ({ slug, onSuccess }, { extra: api }) => {
    const { data } = await api.get(generatePath(ApiRoute.NEWS_SELECTED, { slug }));
    const news = adaptNewsToClient(data);
    onSuccess(news);
});
export const storeNews = createAsyncThunk('news/storeNews', async ({ form, onSuccess }, { extra: api }) => {
    await api.post(ApiRoute.NEWS, form);
    onSuccess();
});
export const updateNews = createAsyncThunk('news/updateNews', async ({ form, onSuccess }, { extra: api }) => {
    const { data } = await api.post('/api/news/update', form);
    onSuccess(adaptNewsToClient(data.news));
});
export const deleteNews = createAsyncThunk('news/deleteNews', async ({ ids, onSuccess }, { extra: api }) => {
    await api.post('/api/news/delete', { ids });
    onSuccess();
});
